import CardCategory from "../enums/CardCategory";
import Deck from "./Deck";
import Evaluator from "./Evaluator";
import Player from "./Player";
import Result from "./Result";
import deckA from "../decks/deck_a";
import deckB from "../decks/deck_b";
import deckC from "../decks/deck_c";
import deckD from "../decks/deck_d";

class Game {
    private _evaluator: Evaluator = new Evaluator();
    private _player: Player = new Player();
    private _deckA: Deck = Deck.readCsv(deckA, CardCategory.A);
    private _deckB: Deck = Deck.readCsv(deckB, CardCategory.B);
    private _deckC: Deck = Deck.readCsv(deckC, CardCategory.C);
    private _deckD: Deck = Deck.readCsv(deckD, CardCategory.D);
    private _isInProgress: boolean = true;

    // constructor(player?: Player, deckA?: Deck, deckB?: Deck, deckC?: Deck, deckD?: Deck) {
    //     this.player = player;
    // }

    public get isInProgress(): boolean {
        if (this.player.choices.cards.length === 100) this.isInProgress = false;
        return this._isInProgress;
    }

    public set isInProgress(value: boolean) {
        this._isInProgress = value;
    }

    public get evaluator(): Evaluator {
        return this._evaluator
    }
    public set evaluator(evaluator: Evaluator) {
        this._evaluator = evaluator;
    }

    public get player(): Player {
        return this._player;
    }
    public set player(player: Player) {
        this._player = player;
    }

    public get result(): Result {
        return this.player.result;
    }

    public get deckA(): Deck {
        return this._deckA;
    }
    public set deckA(deck: Deck){
        this._deckA = deck
    }

    public get deckB(): Deck {
        return this._deckB;
    }
    public set deckB(deck: Deck){
        this._deckB = deck
    }

    public get deckC(): Deck {
        return this._deckC;
    }
    public set deckC(deck: Deck){
        this._deckC = deck
    }

    public get deckD(): Deck {
        return this._deckD;
    }
    public set deckD(deck: Deck){
        this._deckD = deck
    }
     
}

export default Game;